h1 {
    margin: 0px auto 25px auto;
    text-align: center;
    font-weight: 200;
    font-size: 35px;
}

h2 {
    margin: 25px auto 35px auto;
    text-align: center;
    font-weight: 200;
    font-size: 25px;
}

.field-text{
    font-size: 2em;
    color: #505050;
    font-weight: 200;
    margin-top: -10px;
    padding: 8px;
    display: block;
}

.field-text small {
    font-size: 0.5em;
    color: #4E5279;
    font-weight: 400;
    padding: 0px 4px 0px 0px;
    text-align: center;
    margin-top: -12px;
}

.App-link {
  color: #61dafb;
}

@media (max-width: 767px) {
.field-text {
    font-size: 1.5em;
}
.field-text small {
    font-size: 0.4em;
}
}